'use client';

import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { useAuth } from "@/context/auth-context";
import { useRouter } from "next/navigation";
import { useEffect } from "react";
import { Dna, User, GraduationCap, ShieldCheck } from "lucide-react";

export default function LoginPage() {
  const { login, role, isLoading } = useAuth();
  const router = useRouter();

  useEffect(() => {
    if (!isLoading && role) {
      router.push('/dashboard');
    }
  }, [role, isLoading, router]);
  
  if (isLoading || (!isLoading && role)) {
    return (
      <div className="flex h-screen w-full items-center justify-center bg-background">
        <Dna className="h-16 w-16 animate-spin text-primary" />
      </div>
    );
  }

  return (
    <main className="flex min-h-screen flex-col items-center justify-center bg-background p-8">
      <div className="flex flex-col items-center gap-2 mb-8 text-center">
          <h1 className="text-6xl md:text-8xl font-headline text-primary">SchedulO</h1>
          <p className="text-muted-foreground max-w-md">An intelligent timetable management system designed for modern educational institutions.</p>
      </div>
      
      <Card className="w-full max-w-sm shadow-2xl animate-in fade-in zoom-in-95 duration-500">
        <CardHeader>
          <CardTitle className="text-2xl font-semibold text-center">Select Your Role</CardTitle>
          <CardDescription className="text-center">
            Choose your role to proceed to your dashboard.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col gap-4">
            <Button
              className="w-full h-12 text-lg"
              onClick={() => login('faculty')}
            >
              <User className="mr-2 h-5 w-5" />
              Login as Faculty
            </Button>
            <Button
              className="w-full h-12 text-lg"
              variant="secondary"
              onClick={() => login('student')}
            >
              <GraduationCap className="mr-2 h-5 w-5" />
              Login as Student
            </Button>
            <Button
              className="w-full h-12 text-lg"
              variant="outline"
              onClick={() => login('admin')}
            >
              <ShieldCheck className="mr-2 h-5 w-5" />
              Login as Admin
            </Button>
          </div>
        </CardContent>
      </Card>
      <footer className="mt-16 text-center text-sm text-muted-foreground">
        <p>&copy; {new Date().getFullYear()} SchedulO. All rights reserved.</p>
      </footer>
    </main>
  );
}
